* {
  box-sizing: border-box;
  font-family: montserrat;
}

.App {
  position: relative;
  width: 900px;
  max-width: 100%;
  height: 550px;
  border: 1px solid grey;
  margin: auto;
  overflow: hidden;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.left-container {
  position: absolute;
  width: 40%;
  top: 0;
  left: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 3%;
  background-color: #e26553;
  height: 100%;
  color: white;
}

.left-container label {
  font-size: 20px;
}

.left-container input, .left-container select {
  margin: 10px 10px 10px 0;
  font-size: 20px;
}

.left-container input[type=number] {
  width: 50px;
  margin-left: 3%;
  padding: 3px;
}

.right-container {
  position: absolute;
  width: 60%;
  height: 500px;
  right: 0;
  top: 0;
  padding: 3%;
  padding-top: 55px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  font-size: 20px;
  overflow: scroll;
}

article {
  position: relative;
  height: auto;
}

button {
  -webkit-align-self: flex-start;
          align-self: flex-start;
  font-size: 12px;
  background: transparent;
  border: transparent;
  outline: none;
  cursor: pointer;
  color: grey;
  margin-bottom: 20px;
  -webkit-transition: .5s;
  transition: .5s;
}

button:before {
  content: '[ ';
}

button:after {
  content: ' ]';
}

button:active {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}

button:hover {
  color: green;
}

button i {
  padding-left: 10px;
}

.pig {
  width: 60%;
  height: 40%;
  background-image: url(/react-ipsum-generator/static/media/pig.3a59bc82.svg);
  background-repeat: no-repeat;
  margin: auto;
  margin-top: 25%;
}

